<template>
  <div class="data-table-body">
    <template v-for="(row, index) in rows">
      <DataTableGroup v-if="row.children" :key="row.id" :columns="columns" :row="row" :index="index" />
      <div v-else :key="row.id" class="data-table-body-row" @mouseover="mouseMove($event, index)" @mousemove="mouseMove($event, index)" :style="getStyle(index)" >
        <div class="data-table-body-cell" v-for="col in columns" :key="col.field">
          <slot :name="col.type" :col="col" :row="row">
            {{row[col.id]}}
          </slot>
        </div>
        <div class="data-table-body-cell">
            <i class="material-icons drag-handle" @mousedown="drag($event, row, index, $event.target.parentNode.parentNode)">
                drag_handle
            </i>
        </div>
    </div>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    columns: {
      type: Array
    },
    rows: {
      type: Array
    }
  },
  data() {
    return {
      isDragging: false,
      hoverIndex: 0,
      draggedIndex: 0,
      draggedHeight: 0
    };
  },
  methods: {
    getStyle(index) {
      if (this.isDragging) {
        if (this.draggedIndex > this.hoverIndex) {
          return {
            transition: "transform 0.3s ease",
            transform: `translate3d(0, ${
              index >= this.hoverIndex && this.draggedIndex > index
                ? this.draggedHeight
                : 0
            }px, 0)`
          };
        } else if (this.draggedIndex < this.hoverIndex) {
          return {
            transition: "transform 0.3s ease",
            transform: `translate3d(0, -${
              index < this.hoverIndex && this.draggedIndex < index
                ? this.draggedHeight
                : 0
            }px, 0)`
          };
        }
      } else {
        return {};
      }
    },
    mouseMove(e, index) {
      if (!this.isDragging) return;

      var rect = e.target.getBoundingClientRect();
      var y = e.clientY - rect.top;

      if (y <= rect.height / 2) {
        this.hoverIndex = index;
      } else {
        this.hoverIndex = index + 1;
      }
    },
    drag(event, row, index, node) {
      event.preventDefault();

      this.draggedIndex = index;
      this.hoverIndex = index;
      this.isDragging = true;

      const rect = node.getBoundingClientRect();

      var x = event.clientX - rect.left;
      var y = event.clientY - rect.top;

      const height = rect.height;
      const width = rect.width;

      this.draggedHeight = height;

      var clone = node.cloneNode(true);
      var baseStyle = `position: fixed; z-index: 999; width: ${width}px; height: ${height}px; left: -${x}px; top: -${y}px; pointer-events: none`;

      clone.style = `${baseStyle}; transform: translate3d(${event.clientX}px, ${
        event.clientY
      }px, 0);`;

      document.body.appendChild(clone);

      node.classList.add("invisible");

      var mouseMoveHandler = ev => {
        clone.style = `${baseStyle}; transform: translate3d(${ev.clientX}px, ${
          ev.clientY
        }px, 0);`;
      };

      var mouseUpHandler = ev => {
        node.classList.remove("invisible");
        document.body.removeChild(clone);
        document.body.removeEventListener("mousemove", mouseMoveHandler);
        document.body.removeEventListener("mouseup", mouseUpHandler);

        this.isDragging = false;
      };

      document.body.addEventListener("mousemove", mouseMoveHandler);

      document.body.addEventListener("mouseup", mouseUpHandler);
    }
  }
};
</script>