function getJson(url) {
    const headers = new Headers({
        "Content-Type": "application/json"
    });

    return fetch(url, {
        redirect: "follow",
        method: 'GET',
        headers: headers
    }).then(response => {
        if (response.status === 401) {
            window.location.reload();
        }
        else
            return response.json();
    });
}

function postJson(url, body) {
    const headers = new Headers({
        "Content-Type": "application/json"
    });

    return fetch(url, {
        redirect: "follow",
        method: 'POST',
        headers: headers,
        body: body
    }).then(response => {
        if (response.status === 401) {
            window.location.reload();
        }
        else
            return response.json();
    });
}

class StreamReader {
    constructor(url) {
        this.url = url;
        this.cancel = null;
        this.decoder = new TextDecoder("utf-8");
    }

    close() {
        if (this.cancel) {
            this.cancel();
        }
    }

    withParam(param) {
        if (this.cancel) {
            this.cancel();
            this.cancel = null;
        }

        var isCancelled = false;
        var reader = null;

        this.cancel = () => {
            if (!isCancelled) {
                isCancelled = true;

                if (reader) {
                    reader.cancel();
                    reader = null;
                }
            }
        };

        const headers = new Headers({
            "Content-Type": "application/x-ndjson"
        });

        return new Promise((resolve, reject) => {
            fetch(`${this.url}/${encodeURIComponent(param)}`, {
                redirect: "follow",
                method: 'GET',
                headers: headers
            }).then(async response => {
                if (response.status === 200) {
                    if (isCancelled) {
                        reject();
                    }
                    else {
                        reader = response.body.getReader();

                        var items;

                        var prefix = "";

                        for (; ;) {
                            var { done, value } = await reader.read();

                            if (done) break;

                            var string = this.decoder.decode(value);

                            if (prefix.length > 0) {
                                string = prefix + string;
                            }

                            var lastIndex = 0;

                            for (; ;) {
                                var index = string.indexOf("\n", lastIndex);

                                if (index != -1) {
                                    var json = string.substring(lastIndex, index);

                                    lastIndex = index + 1;

                                    const message = JSON.parse(json);

                                    switch (message.event) {
                                        case "initialize":
                                            items = message.data;
                                            resolve(items);
                                            break;
                                        case "create":
                                            items.splice(message.data.offset, 0, message.data.value);
                                            break;
                                        case "update":
                                            items.splice(message.data.from.offset, 1);
                                            items.splice(
                                                message.data.to.offset,
                                                0,
                                                message.data.to.value
                                            );
                                            break;
                                        case "delete":
                                            items.splice(message.data.offset, 1);
                                            break;
                                        case "heartbeat":

                                            break;
                                    }
                                } else break;
                            }

                            if (lastIndex != string.length) {
                                prefix = string.substring(lastIndex, string.length);
                            } else prefix = "";
                        }
                    }
                }
                else if (response.status === 401) {
                    window.location.reload();
                }
                else {
                    reject();
                }
            });
        });
    }
}


function signout() {
    return postJson('/signout')
        .then(response => {
            if (response.status === 200) {

            }
            else {
                return response.text().then(message => {
                    return Promise.reject(message);
                })
            }
        });
}

export default {
    getJson,
    postJson,
    StreamReader,
    signout
}