<style>
.checkbox {
  width: 16px;
  height: 16px;
  min-width: 16px;
  min-height: 16px;
  max-height: 16px;
  max-width: 16px;
  padding: 0;
}

.checkbox .icon {
  font-size: 12px;
  font-weight: bold;
}
</style>

<template functional>
    <button :class="{checkbox: true, primary: props.checked === null || props.checked === true}" v-on="listeners">
        <i class="material-icons icon" v-if="props.checked === true">
            check
        </i>
        <i class="material-icons icon" v-else-if="props.checked === null">
          remove
        </i>
    </button>
</template>

<script>
export default {
  name: "Checkbox",
  props: {
    checked: {
      type: Boolean,
      default: false
    }
  }
};
</script>
