<template>
  <div>
    <div class="list-header">
      <span class="list-content">
        <slot name="header" />
      </span>
      <Button class="small minimal" @click="isOpen = !isOpen">
        <i :class="{'material-icons': true, icon: true, arrow: true, isOpen: isOpen}">
          chevron_right
        </i>
      </Button>
    </div>
    <SlideTransition>
      <div v-show="isOpen">
        <slot />
      </div>
    </SlideTransition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: false
    };
  }
};
</script>

<style>
.list-header {
  display: flex;
  margin: 5px 0;
  cursor: default;
  padding-left: 2px;
  align-items: center;
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  align-content: center;
  text-align: left;
  line-height: 17px;
}

.list-content {
  flex-grow: 1;
}

.list-header .arrow {
  transition: transform 0.3s linear;
  transform: rotate(0deg);
}

.list-header .arrow.isOpen {
  transform: rotate(90deg);
}
</style>