<template>
    <div class="data-table-group">
        <div class="data-table-body-row">
            <div class="data-table-body-cell" v-for="col in columns" :key="col.id">
                {{col.render(row)}}
            </div>
            <div class="data-table-body-cell">
                <Button class="small minimal" @click="isOpen = !isOpen">
                    <i :class="{'material-icons': true, icon: true, arrow: true, isOpen: isOpen}">
                        chevron_right
                    </i>
                </Button>
                <i class="material-icons drag-handle" @mousedown="$parent.drag($event, row, index, $event.target.parentNode.parentNode.parentNode)">
                    drag_handle
                </i>
            </div>
        </div>
        <SlideTransition>
            <div v-if="isOpen">
                <DataTableBody :columns="columns" :rows="row.children" />
            </div>
        </SlideTransition>
    </div>
</template>

<script>
export default {
  props: {
    columns: {
      type: Array
    },
    row: {
      type: Object
    },
    index: {
      type: Number
    }
  },
  data() {
    return {
      isOpen: false
    };
  }
};
</script>

<style>
.data-table-group {
  width: 100%;
}

.data-table-group .arrow {
  transition: transform 0.3s linear;
  transform: rotate(0deg);
}

.data-table-group .arrow.isOpen {
  transform: rotate(90deg);
}
</style>
