<template>
  <div class="data-table">
    <DataTableHeader :columns="columns" :visibleColumns="visibleColumns">
      <template slot="date" slot-scope="{field}">
        <DateRangeInput v-model="range" />
      </template>
    </DataTableHeader>
    <DataTableBody :columns="visibleColumns" :rows="data">
      <template slot="text" slot-scope="{col, row}">
        {{row[col.field]}}
      </template>
      <template slot="date" slot-scope="{col, row}">
        <template v-if="row[col.field]">
          <i class="material-icons table-cell-icon">
            access_time
          </i> {{row[col.field].formatDate()}}
        </template>
      </template>
    </DataTableBody>
    <div class="data-table-footer">
      <div class="data-table-footer-row">
        <div class="data-table-footer-cell" v-for="col in visibleColumns" :key="col.field">
          {{col.label}}
        </div>
        <div class="data-table-footer-cell">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataTableHeader from "./DataTableHeader.vue";

export default {
  props: {
    columns: {
      type: Array
    },
    data: {
      type: Array
    }
  },
  components: {
    DataTableHeader
  },
  computed: {
    visibleColumns() {
      return this.columns.filter(col => col.visible);
    }
  },
  methods: {
    toggleVisibility(col) {
      col.visible = !col.visible;
    }
  }
};
</script>

<style>
.data-table {
  background: #f3f3f3;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2), 0 2px 2px rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.drag-handle {
  cursor: grab;
}

.data-table-header {
  position: sticky;
  top: 0;
  z-index: 2;
}

.data-table-body-row {
  position: relative;
  display: flex;
}

.data-table-header-row {
  position: relative;
  display: flex;
}

.data-table-footer-row {
  position: relative;
  display: flex;
}

.data-table-body-row:nth-child(odd) .data-table-body-cell {
  background: #f9f9f9;
}

.data-table-body-row:hover .data-table-body-cell {
  background: #f5f5f5;
}

.data-table-header-cell:nth-child(2) {
  flex-grow: 1;
}

.data-table-body-cell:nth-child(2) {
  flex-grow: 1;
}

.data-table-footer-cell:nth-child(2) {
  flex-grow: 1;
}

.data-table-footer {
  position: sticky;
  bottom: 0;
  z-index: 1;
}

.data-table-header-cell,
.data-table-body-cell,
.data-table-footer-cell {
  width: 100px;
  flex-shrink: 0;
  padding: 4px 8px;
  display: flex;
  align-items: center;
  font-size: 12px;
}

.data-table-body-cell {
  background: #fff;
  border: 1px solid #cccccc;
  margin-bottom: -1px;
  justify-content: center;
}

.data-table-body-cell:not(:first-child) {
  margin-left: -1px;
}

.data-table-body-cell:first-child, .data-table-header-cell:first-child, .data-table-footer-cell:first-child {
  position: sticky;
  left: 0;
  z-index: 1;
}

.data-table-body-cell:last-child, .data-table-header-cell:last-child, .data-table-footer-cell:last-child {
  position: sticky;
  right: 0;
  z-index: 1;
}

.data-table-header-cell {
  background: var(--primary-color);
  font-weight: bold;
  color: white;
  border: 1px solid #38678f;
  margin-bottom: -1px;
}

.data-table-footer-cell {
  background: #f5f5f5;
  font-weight: bold;
  color: var(--default-color-contrast);
  border: 1px solid #cccccc;
  margin-bottom: -1px;
}

.data-table-header-cell:not(:first-child),
.data-table-footer-cell:not(:first-child) {
  margin-left: -1px;
}

.data-table-body-cell:focus-within {
  z-index: 9999;
  background: white !important;
  box-shadow: 0 0 0 1px var(--primary-color), 0 0 0 3px rgba(19, 124, 189, 0.3),
    inset 0 1px 1px rgba(16, 22, 26, 0.2);
}

.table-cell-icon {
  font-size: 16px;
  width: 20px;
}
</style>
