<template>
  <Button :class="{active: isOpen}" :placement="placement" >
    <Popover ref="popover" trigger="click" @show="isOpen = true" @hide="isOpen = false">
      <slot name="content" />
    </Popover>
    <slot />
  </Button>
</template>

<script>
import Popover from "./Popover.vue";
import Button from "./Button.vue";

export default {
  props: {
    placement: {
      type: String,
      default: "bottom"
    },
  },
  data() {
    return {
      isOpen: false
    };
  },
  methods: {
    close(){
      this.$refs.popover.close();
    }, 
    open(){
      this.$refs.popover.open();
    }
  }
};
</script>