<style>
.overlay-backdrop {
  z-index: 30;
  top: 50px;
  background: rgba(0, 0, 0, 0.4);
  height: calc(100vh - 50px);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transition: background-color 0.25s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay {
  max-width: 100vw;
  max-height: 100vh;
  display: flex;
  flex-direction: column;

  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2),
    0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
}

.overlay-header {
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  background: var(--primary-color);
  color: var(--primary-color-contrast);
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px rgba(0, 0, 0, 0.14),
    0 1px 10px rgba(0, 0, 0, 0.12);
  z-index: 21;
  padding: 10px;
  flex-shrink: 0;
}

.overlay-footer {
  background: white;
  border-top: 1px solid #cccccc;
  flex-shrink: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 2px;
}

.overlay-footer > * {
  margin: 4px;
}

.overlay-body {
  flex-grow: 1;
  background: #fff;
  overflow: auto;
}

.overlay-header-content {
  flex-grow: 1;
}

.overlay-enter,
.overlay-leave-to {
  background-color: rgba(0, 0, 0, 0);
}

.overlay-enter > .overlay {
  opacity: 0;
  transform: scale(0);
}

.overlay-enter-to > .overlay {
  opacity: 1;
  transform: scale(1);
}

.overlay-leave > .overlay {
  opacity: 1;
  transform: scale(1);
}

.overlay-leave-to > .overlay {
  opacity: 0;
  transform: scale(0);
}

.overlay-enter-active > .overlay {
  transition: opacity 0.25s cubic-bezier(0.4, 1, 0.75, 0.9),
    transform 0.25s cubic-bezier(0.4, 1, 0.75, 0.9);
}

.overlay-leave-active > .overlay {
  transition: opacity 0.25s cubic-bezier(0.4, 1, 0.75, 0.9),
    transform 0.25s cubic-bezier(0.4, 1, 0.75, 0.9);
}

@media (min-width: 512px) {
  .overlay {
    width: 512px;
    height: 512px;
  }
}

@media (max-width: 1023px) {
  .overlay {
    width: 100vw;
    height: 100vh;
  }
}
</style>

<template funcional>
  <transition name="overlay">
    <div class="overlay-backdrop" ref="backdrop" v-if="isOpen" @click="$event.target === $refs.backdrop && $emit('close')">
      <div class="overlay">
        <div class="overlay-header">
          <div class="overlay-header-content">
            <slot name="header" />
          </div>
          <Button class="action minimal small" @click="$emit('close')">
            <i class="material-icons icon">
              close
            </i>
          </Button>
        </div>
        <div class="overlay-body">
          <slot />
        </div>
        <div class="overlay-footer">
          <slot name="footer"/>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    isOpen: Boolean,
    default: false
  }
};
</script>
