<template>
    <div class="input-group">
        <DateInput :value="value ? value[0] : null" @input="onInputFrom" :max="value ? value[1] : null" />
        <DateInput :value="value ? value[1] : null" @input="onInputTo" :min="value ? value[0] : null"/>
    </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Array,
      default: null
    }
  },
  methods: {
    onInputFrom(val) {
      this.$emit("input", this.value ? [val, this.value[1]] : [val, null]);
    },
    onInputTo(val) {
      this.$emit("input", this.value ? [this.value[0], val] : [null, val]);
    }
  }
};
</script>