import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import "normalize.css";
import './app.css';
import "material-design-icons/iconfont/material-icons.css"
import "roboto-fontface/css/roboto/roboto-fontface.css"
import "./date-reviver.js";
import './date-utils.js';

import Button from "./components/Button.vue";
import Popover from "./components/Popover.vue";
import DatePicker from "./components/DatePicker.vue";
import DateInput from "./components/DateInput.vue";
import DateRangeInput from "./components/DateRangeInput.vue";
import Suggest from "./components/Suggest.vue";
import BackendSuggest from "./components/BackendSuggest.vue";
import BackendMultipleSuggest from "./components/BackendMultipleSuggest.vue";
import Dropdown from "./components/Dropdown.vue";
import TextInput from "./components/TextInput.vue";
import Tabs from "./components/Tabs.vue";
import DataTable from "./components/DataTable.vue";
import DataTableGroup from "./components/DataTableGroup.vue";
import DataTableBody from "./components/DataTableBody.vue";
import SlideTransition from "./components/SlideTransition.vue";
import CollapsibleList from "./components/CollapsibleList.vue";
import MultipleSuggest from "./components/MultipleSuggest.vue";
import ResizeDirective from "./ResizeDirective.js";
import Overlay from "./components/Overlay.vue"
import Checkbox from "./components/Checkbox.vue"



Vue.component('Overlay', Overlay);
Vue.component('Button', Button);
Vue.component('Popover', Popover);
Vue.component('DatePicker', DatePicker);
Vue.component('DateInput', DateInput);
Vue.component('DateRangeInput', DateRangeInput);
Vue.component('Suggest', Suggest);
Vue.component('MultipleSuggest', MultipleSuggest);
Vue.component('BackendMultipleSuggest', BackendMultipleSuggest);
Vue.component('Dropdown', Dropdown);
Vue.component('TextInput', TextInput);
Vue.component('Tabs', Tabs);
Vue.component('DataTable', DataTable);
Vue.component('DataTableBody', DataTableBody);
Vue.component('DataTableGroup', DataTableGroup);
Vue.component('SlideTransition', SlideTransition);
Vue.component('CollapsibleList', CollapsibleList);
Vue.component('Checkbox', Checkbox);
Vue.component('BackendSuggest', BackendSuggest);




Vue.directive('resize', ResizeDirective);

Vue.config.productionTip = false



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
