<style>
.text-input > input {
  border: none;
  background: none;
  flex-grow: 1;
  color: var(--default-color-contrast);
  margin: 2px;
  line-height: 20px;
  height: 20px;
}

.text-input {
  flex-grow: 1;
}

.text-input > input::placeholder {
  opacity: 1;
  color: rgba(92, 112, 128, 0.5);
}

.text-input > .icon {
  font-size: 16px;
  width: 30px;
  margin: auto;
  position: absolute;
}

.text-input > .action {
  padding: 0;
  margin: 3px;
  justify-content: center;
}

.text-input > .action > .icon {
  font-size: 16px;
}

.text-input:focus-within,
.text-input.active {
  box-shadow: 0 0 0 1px var(--primary-color), 0 0 0 3px rgba(19, 124, 189, 0.3),
    inset 0 1px 1px rgba(16, 22, 26, 0.2);
}

.text-input {
  border-radius: 3px;
  border: 1px solid #e1e2e3;
  display: flex;
  align-items: center;
  background: var(--bg-color);
  padding: 2px;
}

.text-input.minimal {
  box-shadow: none;
  border: none;
  background: none;
}
</style>

<template>
  <div :class="{'text-input': true, minimal: minimal}">
    <slot />
    <slot name="before" />
    <input ref="input" v-bind="$attrs" :value="value" @input="$emit('input', $event.target.value)" v-on="listeners" />
    <slot name="after">
      <Button v-if="clearable && value" class="action minimal small" @click="clear">
        <i class="material-icons icon">
          close
        </i>
      </Button>
    </slot>
  </div>
</template>

<script>
export default {
  props: {
    minimal: {
      type: Boolean,
      default: false
    },
    value: [String, Object, Number],
    clearable: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    listeners() {
      return {
        ...this.$listeners,
        focus: event => {
          this.focused = true;

          this.$emit("focus", event);
        },
        blur: event => {
          this.focused = false;

          this.$emit("blur", event);
        },
        input: event => this.$emit("input", event.target.value)
      };
    }
  },
  methods: {
    clear() {
      this.$emit("input", "");
    },
    focus() {
      this.$refs.input.focus();
    },
    blur() {
      this.$refs.input.blur();
    },
    selectAll() {
      this.$refs.input.select();
    }
  }
};
</script>