<template>
    <div class="data-table-header">
        <div class="data-table-header-row">
            <div class="data-table-header-cell" v-for="col in visibleColumns" :key="col.field">
                <span style="flex-grow: 1">
                    {{col.label}}
                </span>
                <Dropdown class="small minimal">
                    <i class="material-icons icon">
                        filter_list
                    </i>
                    <slot :name="col.type" slot="content"/>
                </Dropdown>
            </div>
            <div class="data-table-header-cell">
                <span style="flex-grow: 1">
                    Ações
                </span>
                <Dropdown class="small minimal">
                    <i class="material-icons icon">
                        settings
                    </i>
                    <ul class="menu" slot="content">
                        <li class="menu-item" v-for="col in columns" :key="col.field" @click="col.visible = !col.visible">
                            <i class="material-icons icon">{{col.visible ? 'check' : ''}}</i><span>{{col.label}}</span>
                        </li>
                    </ul>
                </Dropdown>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    visibleColumns: {
      type: Array,
      required: true,
    },
    columns: {
      type: Array,
      required: true,
    },
  }
};
</script>